#tsparticles {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1;
}

canvas {
  display: block;
  vertical-align: bottom;
}

.heading {
  text-align: center;
  background-color: chocolate;
}
.home {
  background-color: aqua;
}
.wish-list,
.resume,
.interest,
.blog,
.setting { 
  border-radius: 10px;
}
.wish-list:hover,
.resume:hover,
.interest:hover,
.blog:hover,
.setting:hover {
  background-color: lightgreen;
}
